import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CryptoTool {
  constructor() {}

  encryptString(auxString: string) {
    return CryptoJS.AES.encrypt(auxString, environment.cryptoSecret).toString();
  }

  decryptString(auxString: string) {
    return CryptoJS.AES.decrypt(auxString, environment.cryptoSecret).toString(CryptoJS.enc.Utf8);
  }

}
