import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnInit, OnDestroy } from '@angular/core';
import { Router,
         NavigationExtras } from '@angular/router';
import { HomeService } from '../home.service'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  fillerNav = [];

  titleHome: String = 'VisionPi';
  user: any;
  options: any;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher,
    public homeService: HomeService, public router: Router) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    //this.loadConfigurationUser();
  }

  ngOnInit() {
    this.loadConfigurationUser();
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  loadConfigurationUser(): void {
    const session = JSON.parse(localStorage.getItem('session'));
    this.homeService.getInfoUser(session.clientId)
    .subscribe(data => {
      this.user = data;
      this.options = data.platforms.web.options;
      console.log("home service: " + this.user)
      // Get the redirect URL from our auth service
      // If no redirect has been set, use the default
      let redirect = this.options[0].code == "VPDASHBOARD" ? this.options[0].url : this.options[0].url;

      // Set our navigation extras object
      // that passes on our global query params and fragment
      let navigationExtras: NavigationExtras = {
        queryParamsHandling: 'preserve',
        preserveFragment: false
      };

      // Redirect the user
      this.router.navigateByUrl(redirect, navigationExtras);
    });
  }

}
